import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { isObject } from 'util';
import { MessageService } from 'primeng/api';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';

@Component({
  selector: 'app-imagem-display',
  templateUrl: './imagem-display.component.html',
  styleUrls: ['./imagem-display.component.scss'],
})
export class ImagemDisplayComponent implements OnInit {

  @Input()
  src: string = '';
  defaultImg: string = 'assets/imgs/dice.png';

  isZoomed = false;

  constructor(public modalCtrl: ModalController,
    private service: PaginacaoService,
    private toast: MessageService) {
    this.src = this.src || this.defaultImg;
  }

  ngOnInit() {
    const p = this.service.getPromisse();
    if (p) {
      this.src = p;
    }
  }

  close() {
    this.modalCtrl.dismiss(this.src);
  }

  getImg() {
    //console.log(isObject(this.src));
    if (this.src.length < 5 || !this.src || isObject(this.src)) {
      return this.defaultImg;
    } else {
      return this.src;
    }
  }

  getImgZoomed() {
    //console.log(isObject(this.src));
    if (this.src.length < 5 || !this.src || isObject(this.src)) {
      return [{ image: this.defaultImg }];
    } else {
      return [{ image: this.src }];
    }
  }

  update() {
    const p = this.service.getPromisse();
    p.subscribe(res => console.log(res));
  }

  zoom() { // TERMINAR O ZOOM E O MAPA UPDATE
    this.isZoomed = !this.isZoomed;
    // console.log(this.isZoomed);
  }

  showFlag: boolean = false;
  selectedImageIndex: number = -1;
  currentIndex = 0;

  showLightbox(index) {
    this.selectedImageIndex = index;
    this.showFlag = true;
  }

  closeEventHandler() {
    this.showFlag = false;
    this.currentIndex = -1;
  }

}
